/* eslint-disable react/react-in-jsx-scope */
import { Checkbox, FormControlLabel, Radio, RadioGroup, TextField } from '@mui/material'
import { Box } from '@mui/system'
type EntryProps = {
    setValue: any,
    name: any,
    entryList: any,
    setEntryList: any,
    index: number
}

export default function Entry ({ setValue, name, entryList, setEntryList, index }: EntryProps) {
  const setEntryValue = (value: any, type: string) => {
    const newValue = entryList
    newValue[index][type] = value
    setEntryList(newValue)
  }

  return (
        <Box display="flex" flexDirection="row" key={index}>
            <FormControlLabel
                onChange={(event) => setEntryValue((event.target as HTMLInputElement).checked, 'done')}
                name="Done"
                control={<Checkbox />}
                label="J'ai fait"
                key={`done_${index}`}
            />
            <FormControlLabel
                onChange={(event) => setEntryValue((event.target as HTMLInputElement).checked, 'seen')}
                name="Seen"
                control={<Checkbox />}
                label="J'ai observ"
                key={`seen_${index}`}
            />
            <TextField
                onChange={(event) => setEntryValue(event.target.value, 'text')}
                id="outlined-multiline-static"
                label="Dcris l'activit..."
                multiline
                rows={2}
                key={`textarea_${index}`}
                style={{ marginRight: 10 }}
            />
            <RadioGroup row onChange={(event) => setEntryValue(event.target.value, 'liked')} key="liked_radios">
                <FormControlLabel
                    control={<Radio />} label="J'ai aim" value={true}
                    key={`liked_${index}`}
                />
                <FormControlLabel
                    control={<Radio />} label="J'ai moins aim" value={false}
                    key={`less_liked_${index}`}
                />
            </RadioGroup>
            <RadioGroup row onChange={(event) => setEntryValue(event.target.value, 'easy')} key="easy_radios">
                <FormControlLabel
                    control={<Radio />} label="J'ai trouv facile" value={true}
                    key={`easy_${index}`}
                />
                <FormControlLabel
                    control={<Radio />} label="J'ai trouv difficile" value={false}
                    key={`hard_${index}`}
                />
            </RadioGroup>
        </Box>
  )
}
